.prices-section {
    position: relative;
    width: 100vw;
    margin: 0;
    padding: 0;
    height: 600px; /* eller 100vh om du vill ha fullhöjd */
    overflow: hidden;
}
.prices-section::before {
    content: "";
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4); /* justera efter behov */
    z-index: 0;
}

.section-inner {
    max-width: 1200px;
    margin: 0 auto;
}
/* Gör innehållet synligt ovanpå bakgrunden */
.prices-header,
.prices-card-wrapper,
.prices-cta {
    position: relative;
    z-index: 2;
}



/* Bakgrundsbilderna */
.price-bg-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    display: flex;
    z-index: 0;
}

.price-bg {
    flex: 1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}


/* Desktop: visa tre bilder */
.price-bg.desktop {
    flex: 1;
    height: 100%;
    object-fit: cover;
    display: block;
    pointer-events: none;
    max-width: none;
}

/* Mobilbilder – alla utom en är osynliga */
.price-bg.mobile {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 1s ease-in-out;
    z-index: 0;
    display: none;
}

.price-bg.mobile.visible {
    opacity: 1;
}
.prices-title {
    font-size: 2.5rem;
    color: white;
    text-align: center;
    margin-bottom: 0.5rem;
    font-weight: 700;
}
.prices-note {
    font-size: 1.2rem;
    text-align: center;
    color: #f26722;
    margin-bottom: 2rem;
    font-weight: 500;
}
.prices-card-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 30px;
    justify-items: center;
    align-items: start;
    padding: 0 2rem;
    margin-top: 60px;
    gap: 200px 16px;  /* rad-avstånd 100px, kolumn-avstånd 16px */

}



.card-price {
    border: 2px solid rgba(255, 255, 255, 0.15);
    background-color: rgba(0, 0, 0, 0.75);
    border-radius: 20px;
    padding: 20px;
    text-align: center;
    color: white;
    min-width: 220px;
    max-width: 240px;
    box-shadow: 0 6px 20px rgba(0,0,0,0.4);
    backdrop-filter: blur(2px);
    transition: transform 0.3s ease;
    position: relative;
}
.card-price:nth-child(odd) {
    margin-top: 0;
    
}
.card-price:nth-child(even) {
    margin-top: 170px;
}
.card-price:nth-child(1)
{
    margin-right: -500px;
}
.card-price:nth-child(2) {
    margin-right: -250px; /* flyttar lite åt höger */
}

.card-price:nth-child(4){
    margin-left: -250px;
}
.card-price:nth-child(5) {
    margin-left: -500px; /* flyttar lite åt vänster */
}



.card-price:hover {
    transform: scale(1.05);
    border-color: #f26722; }

.prices-cta {
    text-align: center;
    margin-top: 30px;
    position: relative;
    z-index: 2;
}

.prices-cta .btn {
    font-size: 1.1rem;
    padding: 14px 32px;
    border-radius: 30px;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}


/* Mobil: döljer desktopbilder, visar bara en */
@media (max-width: 768px) {
    .price-bg.desktop {
        display: none;
    }

    .price-bg.mobile {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        animation: fadein 1s ease-in-out;
    }
    .price-bg.mobile.visible {
        opacity: 1;
    }
}
/* Swiper justeringar för mobil */
@media (max-width: 768px) {
    .prices-card-wrapper {
        padding: 0 1rem;
        margin-bottom: 120px; /* Ger plats för Boka-knappen längst ner */
    }

    .swiper {
        padding-bottom: 40px;
    }

    .swiper-button-next,
    .swiper-button-prev {
        color: white;
        top: 45%;
        width: 30px;
        height: 30px;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
    }

    .swiper-pagination-bullet {
        background-color: #f26722;
        opacity: 0.7;
    }

    .swiper-pagination-bullet-active {
        background-color: #f26722;
        opacity: 1;
    }

    .prices-cta {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 10;
    }

    .prices-cta .btn {
        padding: 14px 32px;
        font-size: 1rem;
        border-radius: 30px;
        background-color: #f26722;
        color: white;
        box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
    }
}

/* Gör kortet mer levande */
.card-price.popular {
    border: 2px solid #f26722;
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 25px rgba(242, 103, 34, 0.4);
    position: relative;
    transform: translateY(0px) translateX(0px) scale(1.04); /* <-- Placering + grundskala */
    transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.card-price.popular:hover {
    transform: translateY(0px) translateX(0px) scale(1.07); /* <-- Samma placering, bara större */
    box-shadow: 0 0 35px rgba(242, 103, 34, 0.7);
    border-color: #ff7a3e;
}

/* Etikett "Mest populär" */
.popular-badge {
    position: absolute;
    top: 12px;
    right: 12px;
    background-color: #f26722;
    color: white;
    font-size: 0.75rem;
    font-weight: bold;
    padding: 4px 10px;
    border-radius: 20px;
    z-index: 2;
}


/* Mjuk övergång */
@keyframes fadein {
    from { opacity: 0; }
    to { opacity: 1; }
}
@keyframes pulse {
    0%   { transform: scale(1); }
    50%  { transform: scale(1.04); }
    100% { transform: scale(1); }
}
