/* src/css/instructors.css */

.instructor-section {
    padding: 80px 60px;
    color: white;
}

.instructor-section h2 {
    font-size: 2rem;
    margin: 40px 0 20px;
    text-align: center;
    font-family: 'Playfair Display', serif;
}

.instructor-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    justify-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.instructor-card {
    background-color: white;
    color: #111;
    border-radius: 12px;
    overflow: hidden;
    width: 100%;
    max-width: 200px;
    text-align: center;
    padding: 40px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
.instructor-card img {
    width: 100%;
    height: 280px;
    object-fit: cover;
    object-position: center 10%;
    border-radius: 10px;
    margin-top:15px;
}


.instructor-card h3 {
    margin: 10px 0;
    font-size: 1.2rem;
    color: #f26722;
}

.instructor-card .social-icons {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin: 10px 0;
}

.instructor-card .social-icons a {
    font-size: 1.5rem;
    color: #f26722;
    transition: transform 0.3s ease;
}

.instructor-card .social-icons a:hover {
    transform: scale(1.3);
    color: #d5551c;
}

.instructor-card .more-info-btn {
    margin-top: auto;
    background-color: transparent;
    border: 2px solid #f26722;
    color: #f26722;
    border-radius: 25px;
    padding: 10px 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.instructor-card .more-info-btn:hover {
    background-color: #f26722;
    color: white;
}

@media (max-width: 600px) {
    .instructor-grid {
        grid-template-columns: 1fr;
        padding: 0 20px;
    }

    .instructor-card {
        max-width: 100%;
        height: auto;
        padding: 15px;
    }

    .instructor-card img {
        height: auto;
        max-height: 300px;
        object-fit: cover;
        object-position: top;
    }

    .instructor-card .more-info-btn {
        width: 100%;
    }
}
