/* src/css/about.css */

.about-section {
    background: linear-gradient(to right, #1f1f1f, #3a1f1d);
    padding: 60px 20px;
    text-align: center;
}

.about-container {
    max-width: 800px;
    width: 100%;
    padding: 40px;
    margin: 0 auto;
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
}

.about-title {
    font-size: 2.5rem;
    color: #111;
    font-family: 'Playfair Display', serif;
    margin-bottom: 20px;
}

.about-description {
    font-size: 1rem;
    color: #444;
    margin-bottom: 20px;
    line-height: 1.7;
}

.about-image {
    width: 100%;
    max-width: 700px;
    height: auto;
    border-radius: 10px;
    margin-top: 30px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}
@media (max-width: 768px) {
    .about-container {
        padding: 20px;
        margin: 0 auto;
        width: 100%;
    }

    .about-description {
        text-align: left; /* eller justify */
    }

    .about-image {
        width: 100%;
        height: auto;
        border-radius: 8px;
    }
}
