.hero {
    min-height: 100vh;
    background-image: url('../img/BackgroundHero3.png');
    background-size: cover;
    background-position: center;
    padding-top: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

.hero-overlay {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.hero-content {
    width: 100%;
    max-width: 900px;
    padding: 20px;
    border-radius: 12px;
    text-align: center;
    color: white;
    animation: fadeIn 1.5s ease-in-out forwards;
    opacity: 0;
}
.hero-title {
    font-size: 3rem;
    margin-bottom: 10px;
    font-family: 'Playfair Display', serif;
}


.hero-title,
.hero-subtitle {
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}


.hero-subtitle {
    font-size: 1.4rem;
    margin-bottom: 20px;
    font-weight: 300;
}

.hero-buttons {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 2rem;
    flex-wrap: wrap;
}


@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* 🔁 Mobiljusteringar */
@media (max-width: 768px) {
    .hero {
        padding-top: 80px;
        height: auto;
        background-position: top;
        flex-direction: column;
    }

    .hero-title {
        font-size: 2rem;
    }

    .hero-subtitle {
        font-size: 1rem;
        padding: 0 15px;
    }

    .hero-buttons {
        flex-direction: column;
        gap: 15px;
        margin-top: 1.5rem;
    }

    .btn,
    .btn-small {
        width: 100%;
        max-width: 300px;
    }
}